import { useEffect } from "react";

let timer;

const debounce = (callBack, debounceTime) => {

    clearTimeout(timer)

    timer = setTimeout(() => {
        callBack()
    }, debounceTime);

}

let timerHook;

const useDebounce = (count, callback, debounceTime) => {

    useEffect(() => {

        clearTimeout(timerHook)

        timerHook = setTimeout(() => {
            callback()
        }, debounceTime);

    }, [count])
}

export { useDebounce, debounce }